.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: baseline;
  gap: 20px;
  padding: 20px 0;
  display: flex;
}

.photo-card {
  border: 2px solid #eee;
  border-radius: 2px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

img {
  object-fit: cover;
  width: 350px;
  height: 200px;
}

.info {
  justify-content: space-between;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  margin: 10px;
  display: flex;
}

.search-form {
  padding-bottom: 20px 0px;
  background-color: #2d2dfe;
  justify-content: center;
  align-items: center;
  height: 35px;
  display: flex;
}

.load-more-button {
  justify-content: center;
  display: flex;
}

.load-more {
  color: #fff;
  background-color: #2d2dfe;
  border: 1px solid #000;
  border-radius: 2px;
  width: 100px;
  height: 30px;
}

